*{
	box-sizing: border-box;
}
html, body{
	margin: 0;
	padding: 0;
}
html{
	font-size: 15px;
}
body{
	font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
	font-weight: normal;
	// font-size: 15px;
	min-width: 320px;
	position: relative;
	line-height: 1.4;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
	color: $main-font-color;
	background-color: $gray-bg;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid{
	color: red;
}
body input:required:valid,
body textarea:required:valid{
	color: green;
}
input{
	outline: none;
}
.fas, .fa{
	font-size: 16px;
}
.wrapper{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	// align-items: flex-start;
	border-top: 1px solid $gray-line;
	background-color: $gray-line;
}
main{
	width: 100%;
	// flex-basis: 100%;
}
aside.sidebar{
	transition: all .5s;
}
.page-content{
	padding: 20px 40px;

	&__inner{
	max-width: 1050px;

	}

	h1{
		font-weight: normal;
		color: $black;
	}
}
.tokens-buy{
	width: 100%;
	padding: 8px 15px 8px 15px;
	background-color: $green;
	color: #fff;
	a{
		color: #fff;
	}
}
ul, li{
	margin: 0;
	padding: 0;
}
.shadow{
	box-shadow: 0 1px 2px rgba(0,0,0,0.2);
}
.button{
	    cursor: pointer;
    border: 0;
    background: rgb(3, 144, 244);
        padding: 14px 35px;
    font-size: 16px;
    border-radius: 2px;
    color: #fff;
    text-decoration: none;
    display: inline-block;
}
.toggle-mnu{

  display: block;
  width: 28px;
  height: 28px;
  margin-top: 5px;

  span:after, span:before{

    content: "";
    position: absolute;
    left: 0;
    top: 9px;
  } 

  span:after{

    top: 18px;
  }

  span{

    position: relative;
    display: block;
  } 

  span, span:after, span:before{

    width: 100%;
    height: 2px;
    background-color: $main-font-color;
    transition: all 0.3s;
    backface-visibility: hidden;
    border-radius: 2px;
  }


  &.on span{

    background-color: transparent;
  } 

  &.on span:before{

    transform: rotate(45deg) translate(-1px, 0px);
  } 

  &.on span:after{

    transform: rotate(-45deg) translate(6px, -7px);
  } 
}
.formitem{
	border: 1px solid rgba(0,0,0,0.2);
	&:focus{
			border: 1px solid #4dadf7;
			outline: 1px solid #4dadf7;
			position: relative;
			z-index: 1;
		}
}
.burger{
	position: absolute;
	z-index: 100;
	right: 15px;
	top: 15px;
}
.justify{
	content: '';
	width: 100%;
	height: 0;
	display: inline-block;
	overflow: hidden;
	visibility: hidden;
	line-height: 0;
}
.hidden{
	display: none;
}
