.header{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	// height: 90px;
}
.header-logo{
	width: 230px;
	flex-shrink: 0;
	background-color: #fff;
}
.header-content{
	flex-basis: 100%;

	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	font-size: 16px;
}

.header-nav{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
}
.header-tokens,
.header-user,
.header-lang,
.header-account{
	padding: 34px 20px;
	border-left: 1px solid $gray-line;
}
.header-tokens{
	border-left: none;
	.fas{
		margin-right: 10px;
		color: $blue;
	}
}
.header-tokens__val{
	margin-left: 5px;
	color: $blue;
}
.header-lang{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
}
.header-flag{
	width: 20px;
	height: 20px;
	margin-right: 5px;
	background: 0 0 no-repeat;
	background-size: 100%;

	&--rus{
		background-image: url(../img/icons-svg/flags/russia.svg);
	}
}
.header-account{
	.fa-user{
		color: $blue;
		margin-right: 7px;
	}
}