@import "bourbon/_bourbon";
/*==========  Desktop First Method  ==========*/

 

/* Large Devices, Wide Screens */
@media only screen and (max-width : 1300px){
	.token-price__item{
		font-size: 40px;
		line-height: 40px;
		sup{
			font-size: 30px;
		}
	}

}
@media only screen and (max-width : 1200px){
	.token-price__item{
		font-size: 30px;
		line-height: 30px;
		sup{
			font-size: 20px;
		}
	}
	.step-num{
		font-size: 30px;
	}
}
@media only screen and (max-width : 1000px){
	.buy-steps{
		display: block;
	}
	.buy-steps__title{
		text-align: center;
		margin-bottom: 15px;
	}
	.buy-steps__items{
		display: block;
	}

	.buy-steps__items,
	.buy-steps__item{
		background-image: none;
		&:before, &:after{
			display: none;
		}
	}
	.header-content{
		font-size: 12px;
	}
	.header-tokens, .header-user, .header-lang, .header-account {
		padding-top: 20px;
		padding-bottom: 20px;
	}

	.token-info{
		flex-wrap: wrap;
		justify-content: center;
	}
	.token-info__left{
		order: 1
	}
	.token-info__right{
		width: 100%;
		margin-left: 0;
		margin-bottom: 15px;
	}
}
	
/* Medium Devices, Desktops */
@media only screen and (max-width : 992px){
	.header-content{
		display: block;
	}
	.contacts{
		flex-wrap: wrap;
		&-left, &-right{
			width: 100%;
		}
	}
	.contacts-info__val{
		font-size: 16px;
	}
}
	
	
/* Small Devices, Tablets */
@media only screen and (max-width : 768px){

	.header-nav{
		flex-wrap: wrap;
	}
	.sidebar{
		position: absolute;
		left: 0;
		top: 0;
		z-index: 100;
		transform: translateX(-100%);
		max-height: 100vh;
		overflow: auto;
	}
	.header-logo{
		width: 100px;
		display: none;
	}
	.page-content{
		padding: 15px;
	}
	.token-form{
		flex-wrap: wrap;

		label{
			width: 100%;
			margin-bottom: 10px;
		}
	}
	
	.contacts-info__val{
		font-size: 21px;
	}
	.block-icon{
		width: auto;
	}
	.invest-transactions__body{
		overflow: auto;
		table{
			min-width: 768px;
		}
	}
