/* Extra Small Devices, Phones */
@media only screen and (max-width : 480px){
	.header-user{
		display: none;
	}
	.token-price__body{
		flex-direction: column;
		align-items: center;
	}
	.token-price__item{
		order: 1;
	}
	.token-price__prize{
		margin-bottom: 15px;	}
	.token-price__item{
		font-size: 20px;
		line-height: 20px;
		sup{
			font-size: 15px;
		}
	}
	.buy-steps{
		padding: 15px;
	}
	.buy-popup__body{
		padding: 0 15px 15px;
	}
	.buy-popup__buttons .button{
		&:first-child{
			margin-bottom: 10px;
		}
	}
	.contacts-info__val{
		font-size: 16px;
	}
	.help-block,
	.invest-block{
		display: block;
	}
}
		

