.page-content__inner--help{
	width: 100%;
	max-width: 800px;
}
.help-block{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	margin-bottom: 15px;
}
.block-icon{
	padding: 20px;
	width: 200px;
	flex-shrink: 0;
	text-align: center;
	background-color: $icons-bg;

	div{
		margin-bottom: 15px;
		text-transform: uppercase;

	}
	svg{
		font-size: 94px;
	}
}
.block-info{
	flex-basis: 100%;
	padding: 15px 20px;
	background-color: #fff;

	ul{
		li{
			margin-bottom: 10px;
			list-style-type: none;
			a, .fa{
				color: $blue;
			}
			a{
				margin-left: 15px;
			}
		}
	}
}
.help-faq{
	background-color: #fff;
}
.acordeon-item,
.acordeon-subitem{
	list-style-type: none;
}
.acordeon-item{
	border-bottom: 1px dashed $gray-line;
}
.acordeon-subitem{
	padding: 15px 20px 15px 40px;
	background-color: $gray-line;
	color: $black;
}
.help-faq__body{
	padding: 20px;
}
.acordeon-sublist{
	display: none;

	&.opened{
		display: block;
	}
}
.acordeon-link{display: inline-block;
	position: relative;
	padding: 10px 20px 10px 40px;
	color: $blue;
	text-decoration: none;
	&:before{
		content: "\f101";
		position: absolute;
		font-family: 'Font Awesome 5 Free';
		font-weight: 900;
		position: absolute;
		left: 12px;
		top: 10px;
		-webkit-font-smoothing: antialiased;
		display: inline-block;
		font-style: normal;
		font-variant: normal;
		text-rendering: auto;
	}

	&.opened{
		&:before{
			transform: rotate(90deg);
			color: $black;
		}
	}

}