.page-content__inner--contacts{
	width: 100%;
	max-width: 800px;
}
.contacts{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	justify-content: space-between;
	align-items: flex-start;
}
.contacts-block{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	margin-bottom: 15px;
}
.contacts-icon{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: $icons-bg;
}
.contacts-info{
	flex-basis: 100%;
	padding: 15px 20px;
	text-transform: uppercase;
	background-color: #fff;
}
.contacts-info__val{
	text-transform: none;
	&--phone{
		text-decoration: none;
	}
}
.contacts-icon{
	padding: 20px;
	flex-shrink: 0;
	svg{
		font-size: 50px;
	}
}
.contacts-info__title{
	color: $black;
}
.contacts-info__val{
	font-size: 21px;
	color: $blue;
}
.contacts-info__socials{
	a{
		display: inline-block;
		margin-right: 15px;
		margin-bottom: 5px;
		.fab{
			font-size: 22px;
			color: $blue;
		}
	}
}
.contacts-left,
.contacts-right{
	width: 48%;
}
.contacts-right{

	background-color: #fff;
}
.block-header{
	padding: 10px 15px 10px;
	margin-bottom: 10px;
	border-bottom: 1px solid $gray-line;
	text-transform: uppercase;
}
.contacts-right__body{
	padding: 0 15px 15px;

}
.contacts-form{
	input, textarea{
		display: block;
		width: 100%;
		margin-bottom: 10px;
		padding: 5px 10px 6px;
		font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
		font-size: 15px;

	}
	textarea{
		max-width: 100%;
	}
	.button{
		display: block;
		width: 100%;
	}
}