aside{
	width: 230px;
	flex-shrink: 0;

	&.active{
		transform: translateX(0);
	}
}
.menu{
	li{
		a{
			position: relative;
			display: block;
			padding: 15px 20px 15px 66px;
			color: $main-font-color;
			text-decoration: none;
			.fas, .fa, .far{
				position: absolute;
				left: 32px;
				top: 17px;
				margin-right: 15px;
				color: #CCCCCC;
			}
			&:before{
				content: '';
				position: absolute;
				display: none;
				left: 0;
				top: 0;
				width: 5px;
				height: 100%;
				background-color: $blue;

			}
			&:hover, &:active, &:focus{
				color: #000;
				text-decoration: none;

				.fas, .fa, .far{
					color: $blue;
				}

				&:before{
					display: block;
				}
			}


			
		}
	}

	&-main{
		background-color: #fff;
	}
	&-secondary{
		background-color: $menu-bg;

		li{
			a{
				&:hover, &:active, &:focus{
					color: $orange;
					.fas, .fa, .far{
						color: $orange;
					}
				}
				
				&:before{
					background-color: $orange;
				}
			}
		}
	}
}