.page-content__inner--invest{
	width: 100%;
	max-width: 800px;
}
.invest-block{
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
	margin-bottom: 15px;
}
.invest-block__icon{
	svg{
		margin-top: -12px;
	}
}
.wallet-addr{
	margin-bottom: 25px;
}
.wallet-balance__val{
	font-size: 18px;
	color: $black;
}
.invest-transactions{
	background-color: #fff;
}
.invest-transactions__body{
	padding: 15px;
	table{
		width: 100%;
		border-collapse: collapse;
		th, td{
			padding: 8px;
			text-align: left;
			font-weight: normal;


			.far{
				color: $blue;
			}
		}
		tr:nth-child(even){
			background-color: $gray-line;
		}
		.trans-summ,
		.trans-course,
		.trans-tokens {
			text-transform: uppercase;
		}
	}
}